import { useTranslation } from "react-i18next";
import style from "../Income/Income.module.css";
import dayjs from "dayjs";
import { localTimeFormat } from "../../../Utils/Functions/localTime";
const ExpenseCard = ({ expense, setExpenseId }) => {
  const { t } = useTranslation();
  const onDetail = () => {
    setExpenseId(expense?._id);
  };
  return (
    <div onClick={onDetail} className={style.card_container}>
      <div
        className={style.card_container_inner}
        style={{
          width: "50%",
        }}
      >
        <p>{t("recived_from")}</p>
        <h6>
          {expense?.expensePaid
            ? expense?.expensePaid
            : expense?.expensePaidUser?.userLegalName ?? ""}
        </h6>
        <p>
          {" "}
          {expense?.expense_quantity ? expense?.expense_quantity : "N/A"}{" "}
          {t("quantity")}
        </p>
      </div>
      <div
        className={style.card_container_inner}
        style={{
          width: "27%",
        }}
      >
        <p>{t("transaction_id")}</p>
        <h6>N/A</h6>
        <p>
          {" "}
          {dayjs(expense?.createdAt).format("DD MMM YYYY")}
          {", "}
          {localTimeFormat(expense?.createdAt, "LT")}
        </p>
      </div>
      <div
        className={style.card_container_inner}
        style={{
          width: "15%",
        }}
      >
        <p>
          {t("invoice_no")} {expense?.invoice_number ?? ""}
        </p>
        <h6>
          {t("rs_only")} {expense?.expenseAmount ?? ""}
        </h6>
        <p>{expense?.expenseAccount ?? ""}</p>
        <h6></h6>
      </div>
    </div>
  );
};

export default ExpenseCard;
